import React, { Component } from 'react';
import { PacmanLoader } from 'react-spinners';
import "./PhotosPage.css";
import "@fortawesome/fontawesome-free/css/all.css";

class PhotosPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            pics: [""],
            loading: false,
            fullScreen: false,
            imgStyle: {
                backgroundImage: ""
            },
            currentImage: ""
        }

        this.currentLoadedPage = 0;
    }

    goBack = () => {
        this.props.history.push("/");
    }

    getPhotos = () => {
        if (this.currentLoadedPage < 5) {
            this.setState({
                loading: true
            });
            this.currentLoadedPage++;

            const http = new XMLHttpRequest();
            const url = "https://fragaming.com/php-images" + (this.currentLoadedPage > 1 ? this.currentLoadedPage : "") + ".php?";
            http.open("GET", url);
            http.send();
            http.onreadystatechange = (e) => {
                try {
                    if (http.responseText) {
                        //console.log(http.responseText);
                        this.setState({
                            pics: [...this.state.pics, ...JSON.parse(http.responseText)],
                            loading: false
                        });
                    }
                }
                catch {
                    console.log(":)");
                }
            }
        }
    }

    componentDidMount = (prevProps, prevStater, snapshot) => {
        this.getPhotos();
    }

    fullMode = (e) => {
        this.setState({
            fullScreen: true,
            imgStyle: {
                backgroundImage: "url(" + e.currentTarget.src + ")"
            },
            currentImage: e.currentTarget
        });
    }

    closeFullMode = () => {
        this.setState({
            fullScreen: false
        });
    }

    navigateImage = (e) => {
        if (e.keyCode === 37 || e.keyCode === 39) {
            const nextImage = e.keyCode === 39 ? this.state.currentImage.nextSibling : this.state.currentImage.previousSibling;

            this.navigateToImage(nextImage);
        }
        else if (e.keyCode === 27) {
            this.closeFullMode();
        }
    }

    navigateToImage = (nextImage) => {
        if (nextImage) {
            this.setState({
                imgStyle: {
                    backgroundImage: "url(" + nextImage.src + ")"
                },
                currentImage: nextImage
            });
        }
    }

    nextImage = () => {
        const nextImage = this.state.currentImage.nextSibling;
        this.navigateToImage(nextImage);

        if (!nextImage || !nextImage.nextSibling) {
            this.getPhotos();
        }
    }

    previousImage = () => {
        const nextImage = this.state.currentImage.previousSibling;
        this.navigateToImage(nextImage);
    }

    render() {
        return (
            <div className="photos-page">

                {this.state.fullScreen ?
                    <div
                        tabIndex="0"
                        onKeyDown={this.navigateImage}
                        style={this.state.imgStyle}
                        className="full-pic">

                        <span onClick={this.closeFullMode} className="close-full-mode fa fa-times"></span>

                        <div onClick={this.previousImage} className="previous-pic nav-button"><span className="nav-icon fa fa-chevron-left"></span></div>
                        <div onClick={this.nextImage} className="next-pic nav-button"><span className="nav-icon fa fa-chevron-right"></span></div>
                    </div> : null
                }

                <div className={`sweet-loading ${!this.state.loading ? "hidden" : ""}`}>
                    <PacmanLoader
                        sizeUnit={"px"}
                        size={30}
                        color={"yellow"}
                        loading={this.state.loading}
                    />
                </div>

                <div className="photos-header"><button className="home-nav" onClick={this.goBack}>home</button><span>photos</span></div>

                <div className="photos-content">
                    {this.state.pics.map(el =>
                        <img
                            onClick={this.fullMode}
                            className={`my-pic ${el.indexOf("image/png") >= 0 ? 'vertical' : ''}`}
                            src={el} />
                    )}
                </div>
                <div className="footer">
                    <button className="load-more" onClick={this.getPhotos}>Load More Images</button>
                </div>
            </div>
        );
    }
}

export default PhotosPage;
